export default {
	name: 'tuiDatetime',
	props: {
		//1-日期+时间（年月日+时分） 2-日期(年月日) 3-日期(年月) 4-时间（时分） 5-时分秒 6-分秒 7-年月日 时分秒
		type: {
			type: Number,
			default: 1
		},
		//年份区间
		startYear: {
			type: Number,
			default: 1980
		},
		//年份区间
		endYear: {
			type: Number,
			default: 2050
		},
		//"取消"字体颜色
		cancelColor: {
			type: String,
			default: '#888'
		},
		//"确定"字体颜色
		color: {
			type: String,
			default: '#5677fc'
		},
		//设置默认显示日期 2019-08-01 || 2019-08-01 17:01 || 2019/08/01
		setDateTime: {
			type: String,
			default: ''
		},
		//单位置顶
		unitTop: {
			type: Boolean,
			default: false
		},
		//圆角设置
		radius: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			isShow: false,
			years: [],
			months: [],
			days: [],
			hours: [],
			minutes: [],
			seconds: [],
			year: 0,
			month: 0,
			day: 0,
			hour: 0,
			minute: 0,
			second: 0,
			startDate: '',
			endDate: '',
			value: [0, 0, 0, 0, 0, 0],
			reset: false
		};
	},
	mounted() {
		this.initData();
	},
	computed: {
		yearOrMonth() {
			return `${this.year}-${this.month}`;
		},
		propsChange() {
			return `${this.setDateTime}-${this.type}-${this.startYear}-${this.endYear}`;
		}
	},
	watch: {
		yearOrMonth() {
			this.setDays();
		},
		propsChange() {
			this.reset = true;
			setTimeout(() => {
				this.initData();
			}, 10);
		}
	},
	methods: {
		stop() {},
		formatNum: function(num) {
			return num < 10 ? '0' + num : num + '';
		},
		generateArray: function(start, end) {
			return Array.from(new Array(end + 1).keys()).slice(start);
		},
		getIndex: function(arr, val) {
			let index = arr.indexOf(val);
			return ~index ? index : 0;
		},
		//日期时间处理
		initSelectValue() {
			let fdate = this.setDateTime.replace(/\-/g, '/');
			fdate = fdate && fdate.indexOf('/') == -1 ? `2020/01/01 ${fdate}` : fdate;
			let time = null;
			if (fdate) time = new Date(fdate);
			else time = new Date();
			this.year = time.getFullYear();
			this.month = time.getMonth() + 1;
			this.day = time.getDate();
			this.hour = time.getHours();
			this.minute = time.getMinutes();
			this.second = time.getSeconds();
		},
		initData() {
			this.initSelectValue();
			this.reset = false;
			switch (this.type) {
				case 1:
					this.value = [0, 0, 0, 0, 0];
					this.setYears();
					this.setMonths();
					this.setDays();
					this.setHours();
					this.setMinutes();
					break;
				case 2:
					this.value = [0, 0, 0];
					this.setYears();
					this.setMonths();
					this.setDays();
					break;
				case 3:
					this.value = [0, 0];
					this.setYears();
					this.setMonths();
					break;
				case 4:
					this.value = [0, 0];
					this.setHours();
					this.setMinutes();
					break;
				case 5:
					this.value = [0, 0, 0];
					this.setHours();
					this.setMinutes();
					this.setSeconds();
					break;
				case 6:
					this.value = [0, 0];
					this.setMinutes();
					this.setSeconds();
					break;
				case 7:
					this.value = [0, 0, 0, 0, 0, 0];
					this.setYears();
					this.setMonths();
					this.setDays();
					this.setHours();
					this.setMinutes();
					this.setSeconds();
					break;
				default:
					break;
			}
		},
		setYears() {
			this.years = this.generateArray(this.startYear, this.endYear);
			setTimeout(() => {
				this.$set(this.value, 0, this.getIndex(this.years, this.year));
			}, 8);
		},
		setMonths() {
			this.months = this.generateArray(1, 12);
			setTimeout(() => {
				this.$set(this.value, 1, this.getIndex(this.months, this.month));
			}, 8);
		},
		setDays() {
			if (this.type == 3 || this.type == 4) return;
			let totalDays = new Date(this.year, this.month, 0).getDate();
			this.days = this.generateArray(1, totalDays);
			setTimeout(() => {
				this.$set(this.value, 2, this.getIndex(this.days, this.day));
			}, 8);
		},
		setHours() {
			this.hours = this.generateArray(0, 23);
			setTimeout(() => {
				let index = this.type == 5 || this.type == 7 ? this.value.length - 3 : this.value.length - 2;
				this.$set(this.value, index, this.getIndex(this.hours, this.hour));
			}, 8);
		},
		setMinutes() {
			this.minutes = this.generateArray(0, 59);
			setTimeout(() => {
				let index = this.type > 4 ? this.value.length - 2 : this.value.length - 1;
				this.$set(this.value, index, this.getIndex(this.minutes, this.minute));
			}, 8);
		},
		setSeconds() {
			this.seconds = this.generateArray(0, 59);
			setTimeout(() => {
				this.$set(this.value, this.value.length - 1, this.getIndex(this.seconds, this.second));
			}, 8);
		},
		show() {
			setTimeout(() => {
				this.isShow = true;
			}, 50);
		},
		hide() {
			this.isShow = false;
		},
		change(e) {
			this.value = e.detail.value;
			switch (this.type) {
				case 1:
					this.year = this.years[this.value[0]];
					this.month = this.months[this.value[1]];
					this.day = this.days[this.value[2]];
					this.hour = this.hours[this.value[3]];
					this.minute = this.minutes[this.value[4]];
					break;
				case 2:
					this.year = this.years[this.value[0]];
					this.month = this.months[this.value[1]];
					this.day = this.days[this.value[2]];
					break;
				case 3:
					this.year = this.years[this.value[0]];
					this.month = this.months[this.value[1]];
					break;
				case 4:
					this.hour = this.hours[this.value[0]];
					this.minute = this.minutes[this.value[1]];
					break;
				case 5:
					this.hour = this.hours[this.value[0]];
					this.minute = this.minutes[this.value[1]];
					this.second = this.seconds[this.value[2]];
					break;
				case 6:
					this.minute = this.minutes[this.value[0]];
					this.second = this.seconds[this.value[1]];
					break;
				case 7:
					this.year = this.years[this.value[0]];
					this.month = this.months[this.value[1]];
					this.day = this.days[this.value[2]];
					this.hour = this.hours[this.value[3]];
					this.minute = this.minutes[this.value[4]];
					this.second = this.seconds[this.value[5]];
					break;
				default:
					break;
			}
		},
		btnFix() {
			setTimeout(() => {
				let result = {};
				let year = this.year;
				let month = this.formatNum(this.month || 0);
				let day = this.formatNum(this.day || 0);
				let hour = this.formatNum(this.hour || 0);
				let minute = this.formatNum(this.minute || 0);
				let second = this.formatNum(this.second || 0);
				switch (this.type) {
					case 1:
						result = {
							year: year,
							month: month,
							day: day,
							hour: hour,
							minute: minute,
							result: `${year}-${month}-${day} ${hour}:${minute}`
						};
						break;
					case 2:
						result = {
							year: year,
							month: month,
							day: day,
							result: `${year}-${month}-${day}`
						};
						break;
					case 3:
						result = {
							year: year,
							month: month,
							result: `${year}-${month}`
						};
						break;
					case 4:
						result = {
							hour: hour,
							minute: minute,
							result: `${hour}:${minute}`
						};
						break;
					case 5:
						result = {
							hour: hour,
							minute: minute,
							second: second,
							result: `${hour}:${minute}:${second}`
						};
						break;
					case 6:
						result = {
							minute: minute,
							second: second,
							result: `${minute}:${second}`
						};
						break;
					case 7:
						result = {
							year: year,
							month: month,
							day: day,
							hour: hour,
							minute: minute,
							second: second,
							result: `${year}-${month}-${day} ${hour}:${minute}:${second}`
						};
						break;
					default:
						break;
				}
				this.$emit('confirm', result);
				this.hide();
			}, 80);
		}
	}
};
